export const Subtime = function(data){
	let str = String(data).substring(16,24);
		return str;
};

export const Tranobj = function(data){
	let obj = data.substring(0,8),
		obj2 = obj.replace(/:/g,',');
	return obj2;
};

export const timeToArr = function(t){
    return t.toString().split(' ')[4]
};


export const Confimemail = function(str){
	let reg = /\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*/;
		return reg.test(str);
};

export const ConfimPhone = function(str){
	let reg = /^(0|86|17951)?(13[0-9]|15[012356789]|17[678]|18[0-9]|14[57])[0-9]{8}$/;
	return reg.test(str);
};

export const Confimpsd = function(str){
	let reg = /^(?![A-Z]+$)(?![a-z]+$)(?!\d+$)(?![\W_]+$)\S{6,16}$/;
	return reg.test(str);
};

export const Confimshoparea = function(str){
	let reg = /^\d{2,3}(\.\d{2})?$/;
	return reg.test(str);
};

export const Confimcard = function(str){
	let reg = /^(^[1-9]\d{7}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}$)|(^[1-9]\d{5}[1-9]\d{3}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])((\d{4})|\d{3}[Xx])$)$/;
	return reg.test(str);
};

export const Character = function(str){
	let reg = /^[a-zA-Z\u4e00-\u9fa5]*$/;
	return reg.test(str);
};

export const Address = function(str){
	let reg = /^[\u4E00-\u9FA5A-Za-z\d\-\_]{5,30}$/;
	return reg.test(str);
};

export const Busnumber = function(str){
	let reg = /^(?:(?![IOZSV])[\dA-Z]){2}\d{6}(?:(?![IOZSV])[\dA-Z]){10}$/;
	return reg.test(str);
};

export const Resnumber = function(str){
	let reg = /^\d{6}(-)?\d{6}$/;
	return reg.test(str);
};

